.header-nav {
	padding: 1.5rem 0;

	@include media-breakpoint-down(md) {
		transition: background-color 0.15s ease-out;

		&.filled-mobile {
			background-color: $dark-blue;
		}
	}

	@include media-breakpoint-up(lg) {
		padding: 3rem 0;
		transition: padding 0.75s ease-out;

		&.header-mini {
			padding: 1.5rem 0;
		}

		&.filled {
			background: $unit-bg-gradients;
			background-size: $unit-bg-size;
			background-position: $unit-bg-position;
		}
	}
}

.navbar-toggler {
	transition: margin 0.75s ease-out;

	@include media-breakpoint-down(md) {
		margin: calc(0px - #{$navbar-toggler-padding-y} - #{$border-width});
		margin-right: 0;
	}

	@include media-breakpoint-up(lg) {
		.header-mini & {
			margin-top: calc(0.3125rem - #{$navbar-toggler-padding-y} - #{$border-width});
			margin-bottom: calc(0px - #{$navbar-toggler-padding-y} - #{$border-width});
		}
	}
}

.header-logo {
	vertical-align: middle;
	@include media-breakpoint-up(lg) {
		width: auto;
		height: 3.25rem;
		transition: height 0.75s ease-out;

		.header-mini & {
			height: 2rem;
		}
	}
}

.member-icon {
	display: inline-block;
	width: 100%;
	height: 100%;
	stroke: $white;
	transition: stroke 0.25s ease;
}

.member-link {

	display: inline-block;
	border: 1px solid $white;
	width: 2rem;
	height: 2rem;
	border-radius: 50%;
	background: linear-gradient(to right, $white 50%, transparent 50%);
	background-size: 200% 100%;
	background-position: right bottom;
	transition: background-position 0.5s ease, width 0.75s ease-out, height 0.75s ease-out;

	@include media-breakpoint-up(lg) {
		width: 2.75rem;
		height: 2.75rem;

		.header-mini & {
			width: 2rem;
			height: 2rem;
		}
	}


	&:hover,
	&:focus {
		background-position: left bottom;
		outline: 0;

		.member-icon {
			stroke: $link-color;
		}
	}

	[data-whatintent='keyboard'] &:focus {
		box-shadow: 0 0 0 0.25rem rgba(#254a78, 0.6);
	}
}

// offcanvas
.offcanvas {
	 background: linear-gradient(45deg,rgba($white,0.5),rgba($white,0)), linear-gradient(120deg, rgba(#16325a,0) 60%, rgba(#16325a,0.9) 100%), $dark-blue;
}

.offcanvas-header {
	border-bottom: 1px solid rgba($white,0.25);
}

.header-offcanvas-link {
	color: $white;

	&:hover,
	&:focus {
		color: #e8e8e8;
	}
}

@include media-breakpoint-down(md) {
	.offcanvas-header {
		padding-left: $offcanvas-padding-x + $btn-close-width; // offsets center point for logo
	}
}

@include media-breakpoint-up(lg) {
	.offcanvas-header {
		padding: 3rem 4.5rem;
	}

	.offcanvas-body {
		padding: 2.5rem 4.5rem;
	}
}
