.zone-banner {
	position: relative;
	overflow: hidden;
	background-image: url('/images/prospect/banner-clouds.jpg');
	background-size: cover;
}

.banner-video {
	position: absolute;
	top: 0; left: 50%;
	min-width: 100%;
	min-height: 100%;
	transform: translateX(-50%);
	z-index: 2;
	&::-webkit-media-controls-overlay-play-button { // hide button if video doesn't autoplay
		display: none;
	}
}

.banner-content {
	position: absolute;
	top: $header-nav-height-sm; left: 0; right: 0; bottom: 0;
	//background-color: rgba(0,0,0,0.25);
	z-index: 3;

	svg {
		height: 100%;
		width: 100%;
		opacity: 0.9;
	}

	@include media-breakpoint-up(lg) {
		top: $header-nav-height;
	}
}