// cross zone variables
$header-nav-height-sm: 3.5rem;
$header-nav-height: 5.125rem;
$unit-bg-gradients: linear-gradient(45deg,rgba($white,0.5),rgba($white,0)), linear-gradient(120deg, rgba(#16325a,0) 60%, rgba(#16325a,0.9) 100%), $dark-blue;
$unit-bg-size: 100% 100vh;
$unit-bg-position: left top;

// full screen zones
.zone-banner {
	height: 100vh; // fallback
	height: calc(var(--vh, 1vh) * 100);
	width: 100vw;
}

@include media-breakpoint-up(lg) {
	html { 
	  scroll-snap-type: y proximity;
	}

	.zone-banner,
	.zone-units {
		height: 100vh;
		width: 100vw;
	}

	.zone-ci {
		min-height: 100vh;
		width: 100vw;
	}

	.zone-units,
	.zone-ci {
		scroll-snap-align: start;
		scroll-snap-stop: always;
	}

	.zone-banner,
	.footer-nav {
		scroll-snap-align: end;
	}
}
