$menu-height: 10vh;
$border-bottom-height: 4rem;

$slide-content-padding-lg: 2rem;
$slide-content-padding-xl: 3rem;
$slide-content-padding-xxl: 5.5rem;

$control-width: 6.75rem;
$control-padding-lg: 1.5rem;
$control-padding-xl: 2rem;
$control-padding-xxl: 3.5rem;

$animation-speed: 1s;

.zone-units {
	background: $dark-blue;
	
	// accordion styles
	.btn,
	.accordion-button {
		[data-whatintent='keyboard'] &:focus {
			box-shadow: 0 0 0 0.25rem rgba(#254a78, 0.25);
		}
	}

	.accordion-button,
	.accordion-item {
		background-color: transparent;
	}

	.accordion-button {
		width: calc(100% - (#{$accordion-button-padding-x}*2));
		padding-left: 0;
		padding-right: 0;
		margin-left: $accordion-button-padding-x;
		margin-right: $accordion-button-padding-x;
	}

	.accordion-flush .accordion-body {
		padding: 0;
	}

	.accordion .unit-content {
		padding: $accordion-body-padding-y $accordion-body-padding-x; 
	}
}


// carousel styles
@include media-breakpoint-up(lg) {
	.zone-units {
		padding-top: $header-nav-height;
		border-bottom: $border-bottom-height solid $white;
		background: $unit-bg-gradients;
		background-size: $unit-bg-size;
		background-position: $unit-bg-position;

		.carousel {
			height: calc(100vh - (#{$header-nav-height} + #{$border-bottom-height}));
		}

		.carousel-indicators {
			height: $menu-height;
			width: 90vw;
			padding: 3rem 0;
			transform: translateY(5rem);
			opacity: 0;
			transition: transform $animation-speed ease-out, opacity $animation-speed;

			.btn {
				color: $white;
				font-size: $font-size-base;
				border-bottom: 1px solid transparent;

				.active {
					border-bottom-color: $white;
				}
			}
		}

		&.zone-active .carousel-indicators {
			transform: translateY(0);
			opacity: 1;
		}

		.carousel-inner {
			min-height: 66vh;
		}

		.carousel-controls {
			position: absolute;
			top: $menu-height;
			bottom: 0;
			right: $slide-content-padding-lg;
			width: $control-width;
			opacity: 0;
			transition: opacity $animation-speed ease-out;
		}

		&.zone-active .carousel-controls {
			opacity: 1;
		}

		.carousel-control-prev,
		.carousel-control-next {
			border: 1px solid $secondary;
			transition: background-position 0.5s ease;

			.carousel-svg-control {
				fill: $secondary;
				transition: fill 0.25s ease;
			}

			&:hover,
			[data-whatintent='keyboard'] &:focus {
				.carousel-svg-control {
					fill: $primary;
				}
			}

			[data-whatintent='keyboard'] &:focus {
				box-shadow: 0 0 0 0.25rem rgba(#254a78, 0.6);
			}
		}

		.carousel-control-prev {
			background: linear-gradient(to right, $secondary 50%, transparent 50%);
			background-size: 200% 100%;
			background-position: right bottom;

			&:hover,
			[data-whatintent='keyboard'] &:focus {
				background-position: left bottom;
			}
		}

		.carousel-control-next {
			background: linear-gradient(to left, $secondary 50%, transparent 50%);
			background-size: 200% 100%;
			background-position: left bottom;

			&:hover,
			[data-whatintent='keyboard'] &:focus {
				background-position: right bottom;
			}
		}
	}
}

@include media-breakpoint-up(xl) {
	.zone-units {
		.carousel-indicators {
			width: 75vw;
		}
		.carousel-controls {
			right: $slide-content-padding-xl;
		}
	}
}

@media (min-width: 1600px) {
	.zone-units .carousel-controls {
		right: $slide-content-padding-xxl;
	}
}


// carousel contents
.unit-media {
	position: relative;
	opacity: 0;
	transition: opacity $animation-speed ease-in;
	z-index: 2;
	
	@include media-breakpoint-down(md) { // accordion mode
		&.unit-animate-in {
			opacity: 1;
		}
	}

	@include media-breakpoint-up(lg) { // carousel mode
		width: calc( 1.33333 * (90vh - 10rem));
		max-width: 44%;
		max-width: 50%;
		.zone-active .carousel-item.active & {
			opacity: 1;
		}
	}

	@include media-breakpoint-up(xl) {
		max-width: 50%;
	}

	@include media-breakpoint-up(xxl) {
		max-width: 56%;
	}

	.carousel-item-start &,
	.carousel-item-end & {
		opacity: 1;
	}
}

.unit-content {
	font-weight: 300;

	@include media-breakpoint-up(lg) { // carousel mode
		flex: 0 1 66%;
		padding-left: $slide-content-padding-lg;
		padding-right: $control-padding-lg + $control-width + $slide-content-padding-lg;
	}

	@include media-breakpoint-up(xl) {
		flex-basis: 50%;
		padding-left:$slide-content-padding-xl;
		padding-right: $control-padding-xl + $control-width + $slide-content-padding-xl;
	}

	@include media-breakpoint-up(xxl) {
		flex-basis: 44%;
	}

	@media (min-width: 1600px) {
		padding-left: $slide-content-padding-xxl;
		padding-right: $control-padding-xxl+ $control-width + $slide-content-padding-xxl;
	}
}

.unit-content-scroll {}

.unit-content-icon {
	opacity: 0;
	transition: opacity $animation-speed ease-out 0.5s;

	.unit-icon-frame {
		stroke-dasharray: 223.05308; // radius = 35.5; 2𝜋r
		stroke-dashoffset: 223.05308;
		transform-origin: center;
		transform: rotate(-180deg);
		transition: stroke-dashoffset 2s ease-in-out, transform 2s ease-in-out;
	}

	@include media-breakpoint-down(md) { // accordion mode
		&.unit-animate-in {
			opacity: 1;

			.unit-icon-frame {
				stroke-dashoffset: 0;
				transform: rotate(0deg);
			}
		}
	}

	@include media-breakpoint-up(lg) { // carousel mode
		.zone-active .carousel-item.active & {
			opacity: 1;

			.unit-icon-frame {
				stroke-dashoffset: 0;
				transform: rotate(0deg);
			}
		}
	}
}

@media 	(-webkit-min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 1.5) {
	.unit-icon-stroke {
		stroke-width: 1.5;
	}
}

.unit-content-header {
	@include font-size(3rem);
	font-weight: 300;
	line-height: 1.2;
	margin-bottom: $spacer * 1.5;
}

.unit-content-subheader {
	@include font-size(1.25rem);
	line-height: 1.2;
	font-weight: 300;
	margin-bottom: $spacer * 1.5;
}

.unit-content-content {
	margin-bottom: $spacer * 1.5;
}

.unit-content-header,
.unit-content-subheader,
.unit-content-content,
.unit-content-cta {
	opacity: 0;
	transition: opacity $animation-speed ease-out, transform $animation-speed ease-out;

	@include media-breakpoint-down(md) { // accordion mode
		transform: translate(0, 3rem);
	}
	@include media-breakpoint-up(lg) { // carousel mode
		transform: translate(-3rem, 0);

		.unit-content-subheader,
		.unit-content-content {
			transition-delay: 0.5s;
		}

		.unit-content-cta {
			transition-delay: 1s;
		}
	}

	&.unit-animate-in,
	.zone-active .carousel-item.active & {
		opacity: 1;
		transform: translate(0,0);
	}
}

@include media-breakpoint-up(xl) {
	.unit-content-header {
		@include font-size(3.5rem);
		line-height: 1.1;
	}

	.unit-content-subheader {
		@include font-size(1.5rem);
	}
}

@include media-breakpoint-up(xxl) {
	.unit-content-subheader {
		@include font-size(1.75rem);
	}
}

@media (min-width: 1600px) {
	.unit-content-header {
		@include font-size(4.5rem);
	}
}