$zoom-speed: 1s;

.zone-ci {
	padding-top: $header-nav-height;
}

.modal-backdrop.fade {
	transition: opacity $zoom-speed linear;
}
.mosaic {
	position: relative;
	height: calc(40vh + 1rem);
	width: calc(71.11111vh + 1rem);
	margin: 20vh auto 0;

	.btn-close {
		position: absolute;
		top: 1.5rem;
		right: 1.5rem;
		background-color: rgba($white, 0.6);
		z-index: $zindex-modal + 1;
		opacity: 0;
		transition: opacity 0.2s $zoom-speed;
		visibility: hidden;
	}

	&.selection-active .btn-close {
		opacity: $btn-close-opacity;
		visibility: visible;

		&:hover,
		&:focus {
			background-color: rgba($white, 1);
		}
	}
}

.frame {
	position: absolute;
	transition: height, width, left, top, transform;
	transition-duration: $zoom-speed;
	transition-timing-function: ease-in-out;
	border: 0.5rem solid transparent;
	background-color: #eee;
	background-size: cover;
	background-clip: padding-box;
	backface-visibility: hidden;
	
	&.frame-selected {
		top: 0; left: 0;
		width: 100%;
		height: 100%;
		z-index: $zindex-modal;
		//animation: none;
	}
}

.frame-center {
	width: 45%;
	height: 65%;
	left: 42.5%;
	top: 8%;
	transform-origin: left top;
	//animation: float 40s ease-in-out -7s infinite;

	.selection-active &:not(.frame-selected) {
		top: 0;
		left: calc(100% + 0.5rem);
		transform: scale(0.5);
		//animation: shrink $zoom-speed, shrinkfloat 53s ease-in-out $zoom-speed infinite;
	}
}

.frame-north {
	width: 45%;
	height: 50%;
	left: 42.5%;
	top: -42%;
	transform-origin: right bottom;
	//animation: float 50s ease-in-out -16s infinite;

	.selection-active &:not(.frame-selected) {
		top: calc(-50% - 0.5rem);
		left: 55%;
		transform: scale(0.5);
		//animation: shrink $zoom-speed, shrinkfloat 53s ease-in-out $zoom-speed infinite;
	}
}

.frame-east {
	width: 30%;
	height: 56%;
	left: 87.5%;
	top: 12.5%;
	transform-origin: left top;

	.selection-active &:not(.frame-selected) {
		top: 70%;
		left: calc(100% + 0.5rem);
		transform: scale(0.5);
		//animation: shrink $zoom-speed, shrinkfloat 60s ease-in-out $zoom-speed infinite;
	}
}

.frame-southeast {
	width: 30%;
	height: 42%;
	left: 72.5%;
	top: 73%;
	transform-origin: top left;

	.selection-active &:not(.frame-selected) {
		top: calc(100% + 0.5rem);
		left: 100%;
		transform: scale(0.5);
		//animation: shrink $zoom-speed, shrinkfloat 37s ease-in-out $zoom-speed infinite;
	}
}

.frame-south {
	width: 30%;
	height: 70%;
	left: 42.5%;
	top: 73%;
	transform-origin: top left;

	.selection-active &:not(.frame-selected) {
		top: calc(100% + 0.5rem);
		left: 80%;
		transform: scale(0.5);
		//animation: shrink $zoom-speed, shrinkfloat 40s ease-in-out $zoom-speed infinite;
	}
}

.frame-southwest {
	width: 60%;
	height: 74%;
	left: -17.5%;
	top: 25.5%;
	transform-origin: top center;

	.selection-active &:not(.frame-selected) {
		top: calc(100% + 0.5rem);
		left: -30%;
		transform: scale(0.5);
		// animation: shrink $zoom-speed, shrinkfloat 52s ease-in-out $zoom-speed infinite;
	}
}

.frame-northwest {
	width: 45%;
	height: 54%;
	left: -2.5%;
	top: -28.5%;
	transform-origin: left bottom;
	//animation: float 48s ease-in-out infinite;

	.selection-active &:not(.frame-selected) {
		top: calc(-54% - 0.5rem);
		left: -5%;
		transform: scale(0.5);
		//animation: shrink $zoom-speed, shrinkfloat 61s ease-in-out $zoom-speed infinite;
	}
}

@keyframes float {
	0%, 100% {
		transform: translate3d(0px, 0px, 0px);
	}
	20% {
		transform: translate3d(12px, -18px, 1px);
	}
	40% {
		transform: translate3d(-9px, 6px, 2px);
	}
	60% {
		transform: translate3d(6px, 12px, 0px);
	}
	80% {
		transform: translate3d(-16px, 0px, -2px);
	}
}

@keyframes shrink {
	100% {
		transform: perspective(100px)  translate3d(0px, 0px, 2px) scale(0.5);
	}
}

@keyframes shrinkfloat {
	0%, 100% {
		transform: perspective(100px)  translate3d(0px, 0px, 2px) scale(0.5);
	}
	20% {
		transform: perspective(100px)  translate3d(2px, -3px, 0.5px) scale(0.49);
	}
	40% {
		transform: perspective(100px)  translate3d(-1.5px, 1px, 1px) scale(0.51);
	}
	60% {
		transform: perspective(100px)  translate3d(1px, 2px, 0px) scale(0.48);
	}
	80% {
		transform: perspective(100px)  translate3d(-2px, 0px, -1px) scale(0.51);
	}
}